.card-header-card {
  background-color: #d6d7e552;
  color: var(--bs-nav);
  padding: 12px 16px;
  border-radius: 10px 10px 0 0;
  border-bottom: 1px solid #e4e1e1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card-header span {
  color: var(--bs-primary2) !important;
}

.operator {
  background: var(--bs-primary1);
  padding: 4px 10px;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  text-align: center;
  margin: 0px;
  color: var(--bs-primary2);
}

.parent-set h5 {
  margin-bottom: 0px;
  color: var(--bs-primary2);
  font-size: 15px;
}

.nameAndDetails {
  display: flex;

  align-items: center;
}

.avtar-set {
  margin-right: 15px;
}

.avtar-set svg {
  font-size: 45px !important;
}

.parent-set {
  margin-right: 15px;
}

.dateandtime p {
  margin: 0px;
  color: #ffff;
  font-weight: 600;
}

.chat-first {
  border: 1px solid #e4e1e1;
  background: #ffffff;
  border-radius: 10px;
  padding-bottom: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.msg-sec {
  padding: 10px;
}

.card-header-card-2 {
  /* margin-top: 20px; */
  background-color: rgb(245 243 243);
  color: var(--bs-primary2);
}

.dateandtime-2 p {
  color: var(--bs-primary2);
}

.parent-set .operator-2 {
  background-color: var(--bs-primary1);
  color: var(--bs-primary2);
}

.products-details-tab-content {
    background: #e9ecef;
    padding: 12px 16px;
    border-radius: 18px;
    margin-top: 10px;
    line-height: 1.6;
    color: var(--bs-nav);
    display: inline-block;
    max-width: 80%;
    border: 1px solid #d5d8db;
}
